<template>
    <div>
        <el-form :inline="true" :label-position="'right'" label-width="80px" :model="request_data">
            <el-form-item label="诉求人">
                <el-input v-model="request_data.phone_id"></el-input>
            </el-form-item>
<!--            <el-form-item label="表名">-->
<!--                <el-input v-model="request_data.table_name"></el-input>-->
<!--            </el-form-item>-->
            <el-form-item>
                <el-button type="primary" @click="get_data">查询</el-button>
            </el-form-item>
        </el-form>
        <div v-if="draw">
            <el-row type="flex" justify="center">
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'clsi_chart1'" :data="data1" :table_name="'一级分类'"/>
                    </div>
                </el-col>
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'clsi_chart2'" :data="data2" :table_name="'二级分类'"/>
                    </div>
                </el-col>
            </el-row>
            <el-row type="flex" justify="center">
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'clsi_chart3'" :data="data3" :table_name="'三级分类'"/>
                    </div>
                </el-col>
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'clsi_chart4'" :data="data4" :table_name="'四级分类'"/>
                    </div>
                </el-col>
            </el-row>
            <el-row type="flex" justify="center">
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'clsi_chart5'" :data="data5" :table_name="'五级分类'"/>
                    </div>
                </el-col>
            </el-row>
        </div>

    </div>
</template>

<script>
    import pie from "./pie_chart";
    import setting from '../../settings'
    export default {
        name: "classification",
        components: {
            pie,
        },
        props:{
        },
        data: function () {
            return{
                request_data:{
                    phone_id: '',
                    table_name: '',
                },
                url:setting.url,
                data1: [],
                data2: [],
                data3: [],
                data4: [],
                data5: [],
                draw: false,
            }
        },
        methods:{
            get_data(){
                this.draw = true;
                let form = new FormData();
                form.append("phone_id", this.request_data.phone_id);
                form.append("table_name", this.request_data.table_name);
                this.$axios({
                        method: 'post',
                        url: this.url+'label_gjflH/',
                        data:form
                    })
                //     .post(this.url+'label_gjflH/',{
                //         phone_id: this.request_data.phone_id,
                //         table_name: this.request_data.table_name
                // })
                    .then(response =>{
                        this.data1 = response.data['一级分类'];
                        this.data2 = response.data['二级分类'];
                        this.data3 = response.data['三级分类'];
                        this.data4 = response.data['四级分类'];
                        this.data5 = response.data['五级分类'];
                    })
            }
        }
    }
</script>

<style scoped>
    .box_charts_out{
        border-radius: 10px;
        background-color: white;
        box-shadow: 10px 10px 5px #888888;
        margin: 1%;
    }
    .box_charts_in{
        margin: 10%;
    }
</style>