<template>
    <div>
        <el-form :inline="true" :label-position="'right'" label-width="80px" :model="request_data">
            <el-form-item label="诉求人">
                <el-input v-model="request_data.phone_id"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="get_data">查询</el-button>
            </el-form-item>
        </el-form>
        <div v-if="draw">
            <el-row type="flex" justify="center">
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'info_chart1'" :data="data1" :table_name="'紧急程度'"/>
                    </div>
                </el-col>
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'info_chart2'" :data="data2" :table_name="'来电类别'"/>
                    </div>
                </el-col>
            </el-row>
            <el-row type="flex" justify="center">
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <column :id="'info_chart3'" :data="data3" :table_name="'工单状态'"/>
                    </div>
                </el-col>
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <column :id="'info_chart4'" :data="data4" :table_name="'办理单位'"/>
                    </div>
                </el-col>
            </el-row>
            <el-row type="flex" justify="center">
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'info_chart5'" :data="data5" :table_name="'受理渠道'"/>
                    </div>
                </el-col>
                <el-col :span="11" class="box_charts_out">
                    <div class="box_charts_in">
                        <pie :id="'info_chart6'" :data="data6" :table_name="'受理类型'"/>
                    </div>
                </el-col>
            </el-row>
        </div>

    </div>
</template>

<script>
    import setting from '../../settings'
    import pie from "./pie_chart";
    import column from "./column_chart";

    export default {
        name: "order_info",
        components:{
            pie,
            column,
        },
        props:{
        },
        data: function () {
            return{
                request_data:{
                    phone_id: '',
                    table_name: '',
                },
                url:setting.url,
                data1: [],
                data2: [],
                data3: [],
                data4: [],
                data5: [],
                data6: [],
                draw: false,
            }
        },
        methods:{
            get_data() {
                let form = new FormData();
                form.append("phone_id", this.request_data.phone_id);
                this.$axios({
                    method: 'post',
                    url: this.url + 'order_info/',
                    data: form
                })
                    .then(response => {
                        this.data1 = response.data['紧急程度'];
                        this.data2 = response.data['来电类别'];
                        this.data3 = response.data['工单状态'];
                        this.data4 = response.data['办理单位'];
                        this.data5 = response.data['受理渠道'];
                        this.data6 = response.data['受理类型'];
                    });
                this.draw = true;
            }

        }
    }
</script>

<style scoped>
    .box_charts_out{
        border-radius: 10px;
        background-color: white;
        box-shadow: 10px 10px 5px #888888;
        margin: 1%;
    }
    .box_charts_in{
        margin: 10%;
    }
</style>