<template>
    <div>
        <el-form :inline="true" :label-position="'right'" label-width="80px" :model="request_data">
            <el-form-item label="诉求人">
                <el-input v-model="request_data.phone_id"></el-input>
            </el-form-item>
            <el-form-item>
                <div class="block">
                    <el-date-picker
                            v-model="value2"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="right">
                    </el-date-picker>
                </div>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="get_data">查询</el-button>
            </el-form-item>
        </el-form>
        <div v-if="draw">
            <el-row type="flex" justify="center">
                <el-col :span="24" class="box_charts_out">
                    <div class="box_charts_in">
                        <relationship_chart :id="'rela_chart1'" :data="data1" :table_name="'热词关联关系'"></relationship_chart>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import setting from '../../settings'
    import relationship_chart from "./relationship_chart";

    // 格式化Date
    Date.prototype.format = function(fmt) {
        var o = {
            "M+": this.getMonth() + 1,                 //月份
            "d+": this.getDate(),                    //日
            "h+": this.getHours(),                   //小时
            "m+": this.getMinutes(),                 //分
            "s+": this.getSeconds(),                 //秒
            "q+": Math.floor((this.getMonth() + 3) / 3), //季度
            "S": this.getMilliseconds()             //毫秒
        };
        if (/(y+)/.test(fmt))
            fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    };
    export default {
        name: "relationship",
        components:{
            relationship_chart,
        },
        data: function () {
            return{
                url:setting.url,
                draw: false,
                request_data:{
                    phone_id: '',
                },
                data1:[],
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    },{
                        text: '为方便测试输入',
                        onClick(picker) {
                            const start = new Date(2016, 6,1);
                            const end = new Date(2018, 10, 10);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                value2: ''
            }
        },
        methods:{
            get_data(){
                this.draw = true;
                let form = new FormData();
                form.append("phone_id", this.request_data.phone_id);
                let start_time = this.value2[0].format("yyyy-MM-dd hh:mm:ss");
                let end_time = this.value2[1].format("yyyy-MM-dd hh:mm:ss");
                form.append('start_time', start_time);
                form.append('end_time', end_time);
                this.$axios({
                    method: 'post',
                    url: this.url + 'relationship/',
                    data: form
                })
                    .then(response => {
                        this.data1 = response.data['data1'];
                    })
            },
        },
    }
</script>

<style scoped>
    .box_charts_out{
        border-radius: 10px;
        background-color: white;
        box-shadow: 10px 10px 5px #888888;
        margin: 1%;
    }
    .box_charts_in{
        margin: 5%;
    }
</style>