<template>
    <div :id="id" class="main"></div>
</template>

<script>
    export default {
        name: "line_chart",
        props: {
            id: String,
            data: Array,
            table_name: String,
            scale:{
                type: Array,
                default: function () {
                    return [5,10,15,20,25,30]
                }
            }
        },
        watch: {
            data: function () {
                this.drawChart()
            },
        },
        methods: {
            drawChart(){
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById(this.id));
                // 指定图表的配置项和数据
                let option = {
                    title: {
                        text: this.table_name
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    xAxis: {
                        data: this.data.map(function (item) {
                            return item[0];
                        })
                    },
                    yAxis: {
                        splitLine: {
                            show: false
                        }
                    },
                    toolbox: {
                        left: 'center',
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    dataZoom: [{
                        startValue: '2014-06-01'
                    }, {
                        type: 'inside'
                    }],
                    visualMap: {
                        top: 10,
                        right: -5,
                        pieces: [{
                            gt: 0,
                            lte: this.scale[0],
                            color: '#096'
                        }, {
                            gt: this.scale[0],
                            lte: this.scale[1],
                            color: '#ffde33'
                        }, {
                            gt: this.scale[1],
                            lte: this.scale[2],
                            color: '#ff9933'
                        }, {
                            gt: this.scale[2],
                            lte: this.scale[3],
                            color: '#cc0033'
                        }, {
                            gt: this.scale[3],
                            lte: this.scale[4],
                            color: '#660099'
                        }, {
                            gt: this.scale[4],
                            color: '#7e0023'
                        }],
                        outOfRange: {
                            color: '#999'
                        }
                    },
                    series: {
                        name: this.table_name,
                        type: 'line',
                        data: this.data.map(function (item) {
                            return item[1];
                        }),
                        markLine: {
                            silent: true,
                            data: [{
                                yAxis: this.scale[0]
                            }, {
                                yAxis: this.scale[1]
                            }, {
                                yAxis: this.scale[2]
                            }, {
                                yAxis: this.scale[3]
                            }, {
                                yAxis: this.scale[4]
                            }]
                        }
                    }
                };
                myChart.setOption(option);
            }
        },
        mounted() {
            this.drawChart();
        },
    }
</script>

<style scoped>
    .main{
        width: 100%;
        height: 350px;
    }
</style>