<template>
    <div :id="id" class="main"></div>
</template>

<script>
    export default {
        name: "pie",
        props: {
            id: String,
            data:{
                type: Array,
                default: function () {
                    return []
                },
            },
            table_name: String,
        },
        data: function(){
            return{
                data_sorted: [],
            }
        },
        watch: {
            data: function () {
                this.drawChart()
            },
        },
        methods: {
            drawChart(){
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById(this.id));
                let _obj = JSON.stringify(this.data);
                this.data_sorted = JSON.parse(_obj);
                this.data_sorted.sort(function (a, b) { return a.value - b.value; });

                // 指定图表的配置项和数据
                let option = {
                    backgroundColor: '#ffffff',
                    title: {
                        text: this.table_name,
                        left: 'center',
                        top: 10,
                        textStyle: {
                            color: '#000000'
                        }
                    },
                    grid:{},
                    tooltip: {
                        trigger: 'item',
                        formatter: '{a} <br/>{b} : {c} ({d}%)'
                    },

                    visualMap: {
                        show: false,
                        min: 10,
                        max: 600,
                        inRange: {
                            colorLightness: [0, 0.5]
                        }
                    },

                    series: [
                        {
                            name: this.table_name,
                            type: 'pie',
                            radius: '60%',
                            center: ['50%', '50%'],
                            data: this.data_sorted,
                            roseType: 'radius',
                            label: {
                                color: 'rgba(255,22,66,0.89)'
                            },
                            labelLine: {
                                lineStyle: {
                                    color: 'rgba(34,26,255,0.8)'
                                },
                                smooth: 0.2,
                                length: 10,
                                length2: 20
                            },
                            itemStyle: {
                                color: '#722a3d',
                                // shadowBlur: 200,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            },

                            animationType: 'scale',
                            animationEasing: 'elasticOut',
                            animationDelay: function () {
                                return Math.random() * 200;
                            }
                        }
                    ]
                };
                myChart.setOption(option);
            }
        },
        mounted() {
            // this.drawChart();
        },
    }
</script>

<style scoped>
    .main{
        width: 100%;
        height: 350px;
    }
</style>