import h404 from './components/404'
import layout from "./components/layout";
import classification from "./components/classification";
import upload from "./components/upload";
import order_info from "./components/order_info";
import time_trend from "./components/time_trend";
// import index from "./components/index";
import relationship from "./components/relationship";
import word_cloud_page from "./components/word_cloud_page";

export default {
    routes: [
        {
            path: '/404',    //配置访问路径
            name: '404',
            component: h404  //绑定组件
        },
        {
            path: '/',
            component: layout,
            children: [
                {path: '/',name: 'index', component: word_cloud_page, props: true},
                // {path: '/',name: 'index', component: index, props: true},
                {path: '/upload', name: 'upload', component: upload ,props: true},
                {path: '/classification', name: 'classification', component: classification, props: true},
                {path: '/order_info', name: 'order_info', component: order_info, props: true},
                {path: '/time_trend', name: 'time_trend', component: time_trend, props: true},
                {path: '/relationship', name: 'relationship', component: relationship, props: true},
            ]
        }
    ]
}
