<template>
    <div style="width: 500px; height: 500px">
<!--        <form method="post" enctype="multipart/form-data" :action="url+'do_upload'" >-->

<!--        请输入城市名称：<input type="text" name="city" /><br />-->
<!--        请输入分词列名：<input type="text" name="tofenci" /><br />-->
<!--        请输入主键列名：<input type="text" name="gdid" /><br />-->

<!--        <input type="file" name="myfile" />-->
<!--        <br/>-->
<!--        <input type="submit" value="确定上传" />-->
<!--        </form>-->
        <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
            <el-form-item label="表单名称">
                <el-input v-model="formLabelAlign.city"></el-input>
            </el-form-item>
<!--            <el-form-item label="分词列名">-->
<!--                <el-input v-model="formLabelAlign.tofenci"></el-input>-->
<!--            </el-form-item>-->
<!--            <el-form-item label="主键列名">-->
<!--                <el-input v-model="formLabelAlign.gdid"></el-input>-->
<!--            </el-form-item>-->
            <el-upload
                    class="upload-demo"
                    action=""
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :before-remove="beforeRemove"
                    :http-request="uploadSectionFile"
                    multiple
                    :limit="1"
                    :on-exceed="handleExceed"
                    :file-list="fileList"
                    :auto-upload="true">
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">请上传Excel</div>
            </el-upload>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
<!--                <el-button @click="resetForm('ruleForm')">重置</el-button>-->
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import setting from '../../settings'
    export default {
        name: "upload",
        props:{
        },
        data() {
            return {
                labelPosition: 'right',
                formLabelAlign: {
                    city: '',
                    tofenci: '',
                    dgid: ''
                },
                fileList:[],
                url: setting.url,
            };
        },
        methods: {
            uploadSectionFile(param){
                var fileObj = param.file;
                // FormData 对象
                var form = new FormData();
                // 文件对象
                form.append("file", fileObj);
                form.append("city", this.formLabelAlign.city);
                form.append("tofenci", this.formLabelAlign.tofenci);
                form.append("dgid", this.formLabelAlign.dgid);
                this.$axios({
                    method: 'post',
                    url: this.url+'upload_excel/',
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    data:form
                }).then(response => {
                    if(response.data.msg === 'success'){
                        alert('上传成功');
                    this.fileList[0] = response.data.file_id;
                    }
                })
                    // .then(res => {
                    // if(res.data.msgCode === '1'){
                    //     this.$message({
                    //         type:'success',
                    //         message:res.data.msgDesc
                    //     });
                    //     this.attachMentCode = res.data.data.attachmentCode;//附件编码
                    //     this.attachmentId = res.data.data.attachmentId;//标志
                    // } else {
                    //     this.$message({
                    //         type:'error',
                    //         message:res.data.msgDesc
                    //     })
                    // }
                // })
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file) {
                return this.$confirm(`确定移除 ${ file.name }？`);
            },
            submitForm() {
                let fileObj = this.fileList;
                // FormData 对象
                let form = new FormData();
                // 文件对象
                form.append("file_id", fileObj);
                form.append("sheet_name", this.formLabelAlign.city);
                // form.append("tofenci", this.formLabelAlign.tofenci);
                // form.append("dgid", this.formLabelAlign.dgid);
                this.$axios({
                    method: 'post',
                    url: this.url+'persistent_excel/',
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    data:form
                }).then(response =>{
                    if(response.data.msg === 'success')
                        alert('保存成功');
                    else alert('保存失败')
                })
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

