<!--这是一个柱状图组件，使用了Echarts。
    使用请传入：
        id；
        表名：table_name；
        数据：data
-->
<template>
    <div :id="id" class="main"></div>
</template>

<script>
    export default {
        name: "column",
        props: {
            id: String,
            data: {
                type: Array,
                default: function () {
                    return []
                }
            },
            table_name: String,
        },
        watch: {
            data: function () {
                this.drawChart()
            },
        },
        methods: {
            drawChart(){
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById(this.id));
                // 指定图表的配置项和数据
                let option = {
                    title: {
                        text: this.table_name,
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    legend: {
                        // data: [this.data.dimensions[1]]
                        data: [this.table_name]
                    },
                    dataset: {
                        dimensions: ['name', 'value'],
                        source: this.data,
                    },
                    xAxis: {
                        type: 'category',
                        // axisLabel: {rotate: 0, interval: 0}
                    },
                    yAxis: {},
                    grid: {
                        left: '11%',
                        // right: '10%',
                        // bottom: '10%',
                        // top: '10%'
                    },
                    series: [
                        {
                            name: this.table_name,
                            type: "bar",
                            color: ['#3398DB', '#dd6b66', '#759aa0', '#e69d87', '#8dc1a9', '#ea7e53', '#eedd78', '#73a373', '#73b9bc', '#7289ab', '#91ca8c', '#f49f42'],
                        },
                    ]
                };
                myChart.setOption(option);
            }
        },
        mounted() {
            // this.drawChart();
        },
    }
</script>

<style scoped>
.main{
    width: 100%;
    height: 350px;
}
</style>